.mainDiv {
	display: flex;
	flex-direction: column;
	margin-top: 50px;
	padding: 100px;
	justify-content: center;
	align-items: center;
}

.profile {
	padding: 3em;
	box-shadow: 1px 5px 7px 5px #f3f3f3;
	border-radius: 10px;
	width: 40vw;
}

.actionDiv {
	margin: 1em;
	cursor: pointer;
}
.actionDiv p {
	color: grey;
}
.actionDiv i {
	color: #34b6b4;
	margin: 0.6em;
}
#user {
	display: flex;
	justify-content: center;
	margin-bottom: 3.5em;
}
#user span {
	font-size: 1.3em;
	color: #230a38;
	text-transform: uppercase;
}
@media only screen and (max-width: 600px) {
	.profile {
		width: 90vw;
	}
}
